import React from 'react';
import TextField from '@material-ui/core/TextField';
import Autocomplete from '@material-ui/lab/Autocomplete';

export default function LocationBox(props) {

  function handleLocationChange(event,value) {
    // Here, we invoke the callback with the new value
    props.onChange(value);
}

  return (
    <Autocomplete
      id="location-box"
      options={locations.sort((a,b) => (a.type> b.type) ? 1 : ((b.type> a.type) ? -1 : 0) || a.location.localeCompare(b.location) )}
      getOptionLabel={(option) => option.location}
      groupBy={(option=>option.type)}
      style={{ margin: 'auto', marginTop: 20,
      width: '50%', width: 300, position: 'relative', top: '20px',WebkitTextFillColor:"black"}}
      renderInput={(params) => <TextField {...params} label="Select Continent/Country/State" variant="outlined" />}
      onChange={handleLocationChange}
    />
  );
}

