import { useD3 } from '../hooks/useD3';
import React from 'react';
import ReactDOM from 'react-dom';
import * as d3 from 'd3';

function EmissionsGraph({countryEmission}) {
    const ref = useD3(
        (svg) => {
          const height = 500;
          const width = 500;
          const margin = { top: 20, right: 30, bottom: 30, left: 40 };

          const x = d3
            .scaleBand()
            .domain(countryEmission.map((d) => d.year))
            .rangeRound([margin.left, width - margin.right])
            .padding(0.1);

          const y1 = d3
            .scaleLinear()
            .domain([0, d3.max(countryEmission, (d) => d.emission)])
            .rangeRound([height - margin.bottom, margin.top]);

          const xAxis = (g) =>
            g.attr("transform", `translate(0,${height - margin.bottom})`).call(
              d3
                .axisBottom(x)
                .tickValues(
                  d3
                    .ticks(...d3.extent(x.domain()), width / 40)
                    .filter((v) => x(v) !== undefined)
                )
                .tickSizeOuter(0)
            );

          const y1Axis = (g) =>
            g
              .attr("transform", `translate(${margin.left},0)`)
              .style("color", "steelblue")
              .call(d3.axisLeft(y1).ticks(null, "s"))
              .call((g) => g.select(".domain").remove())
              .call((g) =>
                g
                  .append("text")
                  .attr("x", -margin.left)
                  .attr("y", 10)
                  .attr("fill", "currentColor")
                  .attr("text-anchor", "start")
                  .text(countryEmission.y1)
              );

          svg.select(".x-axis").call(xAxis);
          svg.select(".y-axis").call(y1Axis);

          svg
            .select(".plot-area")
            .attr("fill", "steelblue")
            .selectAll(".bar")
            .data(countryEmission)
            .join("rect")
            .attr("class", "bar")
            .attr("x", (d) => x(d.year))
            .attr("width", x.bandwidth())
            .attr("y", (d) => y1(d.emission))
            .attr("height", (d) => y1(0) - y1(d.emission));

          svg.append("text")
          .attr("transform", "translate(" + (width/2) + " ," + (height+30) + ")")
          .style("text-anchor", "middle")
          .text("Year");

          svg.append("text")
          .attr("transform", "rotate(-90)")
          .attr("class", "y label")
          .attr("x", -height/2)
          .attr("dy", ".75em")
          .style("text-anchor", "middle")
          .text("Carbon Emissions (Tons)");
        },
        [countryEmission.length]
      );

      return (
        <svg
          ref={ref}
          style={{
            position: 'relative',
            left: '36.5%',
            margin: 'auto',
            height: 800,
            width: '100%',
            marginRight: "0px",
            marginLeft: "0px",
          }}
        >
          <g className="plot-area" />
          <g className="x-axis" />
          <g className="y-axis" />
        </svg>
      );
}

  export default EmissionsGraph;